
function ProviderProfile() {
    return (
        <>
        <div className="profile-container">
        <div className="profile-header">
            <div className="profile-picture">
                <img src="images/placeholder2.png" alt="Profile Picture" />
            </div>
            <div className="profile-details">
                <h1>John Doe</h1>
                <p className="specialty">Family Law Attorney</p>
                <p className="location">New York, NY</p>
                <div className="contact-info">
                    <p>Email: <a href="mailto:john.doe@example.com">john.doe@example.com</a></p>
                    <p>Phone: (123) 456-7890</p>
                    <p className="card-text">Rating: <span className="stars">&#9733; &#9733; &#9733; &#9733; &#9733;</span></p>
                <p><span>Experience: </span>15+ years</p>
                </div>
            </div>
        </div>
        <div className="profile-description">
            <h2>About Me</h2>
            <p>
                John Doe is a highly experienced family law attorney based in New York City.
                With over 15 years of practice, John has helped numerous clients navigate
                complex legal issues related to divorce, child custody, and more.
            </p>
        </div>
        <div className="profile-services">
            <h2>Services Offered</h2>
            <ul>
                <li>Divorce and Separation</li>
                <li>Child Custody</li>
                <li>Legal Consultation</li>
                <li>Mediation Services</li>
            </ul>
        </div>
        <div className="profile-reviews">
            <h2>Client Reviews</h2>
            <div className="review">
                <p>
                    "John Doe is an outstanding attorney. His knowledge, dedication, and
                    professionalism are truly exceptional. He helped me achieve a favorable
                    outcome in my divorce case."
                </p>
                <p className="review-author">- Sarah Johnson</p>
            </div>
            <div className="review">
                <p>
                    "I had the pleasure of working with John, and I couldn't be happier with
                    the results. He is a compassionate and skilled attorney who genuinely
                    cares about his clients."
                </p>
                <p className="review-author">- Michael Smith</p>
            </div>

            <button type="submit" className="default-button contact-button">
            Request Chat
          </button>

        </div>
    </div>
        </>
    );
}

export default ProviderProfile