function Navbar() {
    return (
      <nav className="navbar navbar-expand-lg navbar-light bg-body-dark">
        <div className="container-fluid">
          <a className="navbar-brand">LegalAdvisor</a>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarNavAltMarkup"
            aria-controls="navbarNavAltMarkup"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
            <div className="navbar-nav ms-auto">
            <div className="nav-item" ><a className="nav-link" href="/">Home</a></div>
            <div className="nav-item" ><a className="nav-link" href="services">Services</a></div>
            <div className="nav-item" ><a className="nav-link" href="legal-providers">Legal Providers</a></div>
            <div className="nav-item" ><a className="nav-link" href="about">About Us</a></div>
            <div className="nav-item" ><a className="nav-link" href="contact">Contact</a></div>
            </div>
          </div>
        </div>
      </nav>
    );
  }
  
  export default Navbar;