import Navbar from "./Navbar";
import Home from "./Home";
import Services from "./Services";
import About from "./About";
import AboutPage from "./AboutPage";
import Contact from "./Contact";
import Footer from "./Footer";
import Provider from "./Provider";
import ClientLoginForm from "./ClientLoginForm";
import ProviderLoginForm from "./ProviderLoginForm";
import ClientSignupForm from "./ClientSignupForm";
import ProviderSignupForm from "./ProviderSignupForm";
import ProviderProfile from "./ProviderProfile";
import ProviderDashboard from "./ProviderDashboard";
import { HashRouter, Routes, Route, BrowserRouter } from "react-router-dom";

function App() {
  return (
    <BrowserRouter>

      <Routes>

      <Route exact path="/" element={

      <>
      <Navbar />
      <Home/>
      <About/>
      <Services/>
      <Contact/>
      </>
      
      }></Route>

      <Route exact path="/legal-advisors/#services" element={ <><Navbar /> <Services/></> }></Route>

      <Route exact path="/legal-advisors/#about" element={ <><Navbar/> <AboutPage/></> }></Route>

      <Route exact path="/legal-advisors/#contact" element={ <><Navbar/> <Contact/></> }></Route>

      <Route exact path="/legal-advisors/#legal-providers" element={ <><Navbar/> <Provider/></> }></Route>

      <Route exact path="/legal-advisors/#client-login" element={ <><Navbar/> <ClientLoginForm/></> }></Route>

      <Route exact path="/legal-advisors/#provider-login" element={ <><Navbar/> <ProviderLoginForm/></> }></Route>

      <Route exact path="/legal-advisors/#client-signup" element={ <><Navbar/> <ClientSignupForm/></> }></Route>

      <Route exact path="/legal-advisors/#provider-signup" element={ <><Navbar/> <ProviderSignupForm/></> }></Route>

      <Route exact path="/legal-advisors/#provider-profile" element={ <><Navbar/> <ProviderProfile/></> }></Route>

      <Route exact path="/legal-advisors/#provider-dashboard" element={ <ProviderDashboard/> }></Route>

      </Routes>
      <Footer />

    </BrowserRouter>

  );
}

export default App;
