import LocationOnOutlinedIcon from "@mui/icons-material/LocationOnOutlined";
import PublicOutlinedIcon from "@mui/icons-material/PublicOutlined";
import LocalPhoneOutlinedIcon from "@mui/icons-material/LocalPhoneOutlined";
import EmailOutlinedIcon from "@mui/icons-material/EmailOutlined";

function Contact() {
  return (
    <section id="contact" className="contact-section">
      <h1 className="contact-heading">Contact Us</h1>

      <div className="contact-box row">

      <div className="contact-details col">
        <h3 className="mb-3">Reach out</h3>
        <p>
          Lorem ipsum dolor sit amet, consectetur adipiscing elit. Ut semper
          augue quis purus sodales
        </p>

        <div className="contact-details-element d-flex align-items-center">
          <LocationOnOutlinedIcon className="contact-icon" />
          <p className="contact-details-para"> <span className="dark-text">Address:</span> Indore, India</p>
        </div>

        <div className="contact-details-element d-flex align-items-center">
          <EmailOutlinedIcon className="contact-icon" />
          <p className="contact-details-para"><span className="dark-text">Email:</span> vineettirole@gmail.com</p>
        </div>

        <div className="contact-details-element d-flex align-items-center">
          <LocalPhoneOutlinedIcon className="contact-icon" />
          <p className="contact-details-para"><span className="dark-text">Phone:</span> 89xxxxxxx4</p>
        </div>

        <div className="contact-details-element d-flex align-items-center">
          <PublicOutlinedIcon className="contact-icon" />
          <p className="contact-details-para"> <span className="dark-text">Website:</span> www.vineett.com</p>
        </div>

      </div>

      <div className="form-box col">
      <h3 className="mb-3">Send us a message</h3>

        <form className="contact-form">
          <div className="row mb-3">
            <div className="col">
              <input
                type="text"
                className="form-control contact-input"
                placeholder="Full name"
              ></input>
            </div>

            <div className="col">
              <input
                type="email"
                className="form-control contact-input"
                placeholder="Email address"
              ></input>
            </div>
          </div>

          <div className="row mb-3">
            <div className="col">
              <input
                type="text"
                className="form-control contact-input"
                placeholder="Email Subject"
              ></input>
            </div>

            <div className="col">
              <input
                type="number"
                className="form-control contact-input"
                placeholder="Phone number"
              ></input>
            </div>
          </div>

          <div className="mb-3">
            <textarea
              className="form-control contact-input"
              rows="5"
              placeholder="Message"
            ></textarea>
          </div>

          <button type="submit" className="default-button contact-button">
            Send Message
          </button>
        </form>
      </div>

      </div>

    </section>
  );
}

export default Contact;
