function Home() {
    return (
      
        <div className="container-fluid home-box row">
          <div className="col-md-6 home-heading">
            <h1>Welcome to Legal Services eMarketplace</h1>
            <h3>Your trusted destination for legal assistance. </h3>
            <div className="home-buttons">
            <button type="button" className="btn btn-md download-button light-button" onClick={() => window.location.href = '/client-signup'}>Sign-up as Client</button>
        <button type="button" className="btn btn-dark btn-md download-button black-button" onClick={() => window.location.href = '/provider-signup'}>Sign-up as Provider</button>
        </div>
          </div>
          <div className="col-md-6 home-image">
            <img src="images/sih1.png" alt="unable to load"></img>
          </div>
        </div>
    )
    }

export default Home