import SearchIcon from '@mui/icons-material/Search';
import KeyboardArrowLeftIcon from '@mui/icons-material/KeyboardArrowLeft';
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';
// import ArrowUpwardIcon from '@mui/icons-material/ArrowUpward';
import ProviderCard from './ProviderCard';

function Provider() {
    return (
        <section className="news-section d-flex justify-content-center align-items-start row">
        <div className="news-heading">
        <h1 className="text-center">Search Legal Advisors</h1>
        <form className="d-flex search-box mx-auto" role="search">
                  <input
                    className="form-control"
                    type="search"
                    placeholder="Search"
                    aria-label="Search"
                  />
                  <button className="btn btn-primary search-button" type="submit">
                    <SearchIcon />
                  </button>
                </form>
        </div>
      
      <div className='d-flex'>

  <div className="dropdown d-flex filter-box">
  <p className='filter-text'>Filter by: </p>
  <button className="btn btn-secondary btn-sm dropdown-toggle filter-button" type="button" data-bs-toggle="dropdown" aria-expanded="false">
    Default
  </button>
  <ul className="dropdown-menu">
    <li><a className="dropdown-item" href="#">Location</a></li>
    <li><a className="dropdown-item" href="#">Expertise</a></li>
    <li><a className="dropdown-item" href="#">Rating</a></li>
  </ul>
</div>

<div className="dropdown d-flex filter-box">
  <p className='filter-text'>Category: </p>
  <button className="btn btn-secondary btn-sm dropdown-toggle filter-button" type="button" data-bs-toggle="dropdown" aria-expanded="false">
    Default
  </button>
  <ul className="dropdown-menu">
    <li><a className="dropdown-item" href="#">Advocates and Lawyers</a></li>
    <li><a className="dropdown-item" href="#">Arbitrators and Mediators</a></li>
    <li><a className="dropdown-item" href="#">Notaries</a></li>
    <li><a className="dropdown-item" href="#">Legal Consultants</a></li>
    <li><a className="dropdown-item" href="#">Corporate Counsel</a></li>
  </ul>
</div>

</div>

        <ProviderCard />
        <ProviderCard />
        <ProviderCard />
        <ProviderCard />
        <ProviderCard />
        <ProviderCard />
        <ProviderCard />
        <ProviderCard />

        <div className="page-div d-flex align-items-center justify-content-center">
            <button className="btn btn-primary default-button">
              <KeyboardArrowLeftIcon />
            </button>
            <p className='pageNumber'>Page: 1</p>
            <button className="btn btn-primary default-button">
              <KeyboardArrowRightIcon />
            </button>
          </div>

        </section>
    );
}

export default Provider